import { LoaderFunction } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import { Client, Entity, Schema } from 'redis-om'

class Person extends Entity {}

const personSchema = new Schema(Person, {
  firstName: { type: 'string' },
  lastName: { type: 'string' }
})

export const loader: LoaderFunction = async () => {

  const url = "redis://default:IGR4niFMT9eR0LXMPu7rG0iRSSVg0XZ3@redis-15245.c273.us-east-1-2.ec2.cloud.redislabs.com:15245";
  const client = await new Client().open(url)

  const personRepository = client.fetchRepository(personSchema)
  await personRepository.createIndex()

  const person = await personRepository.createAndSave({
    firstName: 'kal',
    lastName: 'el'
  })


  return { name: "1", person }
}

export default function Index() {
  const data = useLoaderData<typeof loader>();
  console.log(data);
  return (
    <div style={{ fontFamily: "system-ui, sans-serif", lineHeight: "1.4" }}>
     <h1>Datos</h1>
     <p>{data.name}</p>
    </div>
  );
}
